declare var app: any

export namespace RiesgoModel {
  export function calculateRiesgoInherente(riesgo: ProyectoRiesgo): number {
    if (!riesgo.probabilidad || !riesgo.impacto) {
      return
    }

    const p_i = +riesgo.probabilidad + riesgo.impacto
    if (p_i == 10 || p_i == 8) {
      return 5
    } else if (p_i == 6) {
      return 3
    } else {
      return 1
    }
  }

  export function calculateControlValoracion(riesgo: ProyectoRiesgo, criterioNamelist: string[]): 1 | 3 | 5 {
    let control_valoracion: number = 0

    _.each(criterioNamelist, (criterio) => {
      if (!(criterio in riesgo)) {
        return
      }

      let valoracion = (riesgo as any)[criterio]
      if (valoracion === true) {
        valoracion = 1
      } else if (valoracion === false) {
        valoracion = 0
      }

      if (valoracion === '') {
        control_valoracion = 0
        return false
      }

      valoracion = parseFloat(valoracion)
      if (isNaN(valoracion)) {
        control_valoracion = 0
        return false
      }

      control_valoracion += valoracion
    })

    if (control_valoracion == 0) {
      return null
    } else if (control_valoracion <= 5) {
      return 1
    } else if (control_valoracion <= 7) {
      return 3
    } else if (control_valoracion <= 10) {
      return 5
    }
  }

  export function calculateRiesgoSolidez(riesgo: ProyectoRiesgo): 1 | 3 | 5 {
    if (isNaN(parseInt('' + riesgo.control_efectividad))) {
      return null
    }

    if (riesgo.control_valoracion === null) {
      return null
    }

    const valoracion_efectividad = +riesgo.control_valoracion + +riesgo.control_efectividad
    switch (valoracion_efectividad) {
      case 10:
        return 5
      case 8:
        return 3
      default:
        return 1
    }
  }

  export function calculateRiesgoResidual(riesgo: ProyectoRiesgo): 1 | 3 | 5 {
    if (isNaN(parseInt('' + riesgo.control_efectividad)) && riesgo.control_valoracion === null) {
      return riesgo.riesgo_inherente
    }

    const solidez = calculateRiesgoSolidez(riesgo)
    if (solidez === null) {
      return null
    }

    if (riesgo.riesgo_inherente == 1 || solidez == 5) {
      return 1
    } else if (solidez == 3 || (solidez == 1 && riesgo.riesgo_inherente == 3)) {
      return 3
    } else {
      return 5
    }
  }

  export function create(
    riesgo: Partial<ProyectoRiesgo> | { bibliotecaIds: string[] }
  ): Promise<ProyectoRiesgo | ProyectoRiesgo[]> {
    return new Promise<ProyectoRiesgo>((resolve, reject) => {
      const params = 'bibliotecaIds' in riesgo ? riesgo : { fields: riesgo }
      $.post(url_for(`proyecto/${app.vars.idproyecto}/riesgos/create`), params)
        .done((res) => {
          if (!res.hasErrors) {
            resolve('records' in res.response ? res.response.records : res.response.record)
          }
        })
        .fail(reject)
    })
  }

  export function setChanges(riesgo: ProyectoRiesgo, changes: any) {
    changes.control_valoracion = calculateControlValoracion(
      Object.assign({}, riesgo, changes),
      _.keys(app.vars.riesgocontext.criterios_evaluacion)
    )
    if (!changes.control_valoracion) {
      changes.control_efectividad = null
    }
    changes.riesgo_residual = calculateRiesgoResidual(Object.assign({}, riesgo, changes))

    let modified = false
    _.each(changes, (value, key: keyof ProyectoRiesgo) => {
      if (riesgo[key] != value) {
        modified = true
        return false
      }
    })

    return !modified ? null : changes
  }

  export function getChangesWhenRemoveControlCodigo(riesgo: ProyectoRiesgo) {
    let changes: any = { control_codigo: null, control_descripcion: null, control_efectividad: null }
    _.each(_.keys(app.vars.riesgocontext.criterios_evaluacion), (criterio) => {
      changes[criterio] = null
    })

    return changes
  }

  export function save(riesgo: ProyectoRiesgo, changes: any) {
    return new Promise((resolve, reject) => {
      if (changes === null) {
        resolve()
        return
      }
      $.post(url_for(`/proyecto/${app.vars.idproyecto}/riesgos/${riesgo.id}`), {
        fields: changes
      }).done((res) => {
        if (res.success) {
          resolve()
        } else {
          reject(res.message)
        }
      })
    })
  }
}
