import { PureComponent, Fragment } from 'react'

import Menu from 'semantic-ui-react/dist/commonjs/collections/Menu/Menu'

import { ProyectoRiesgoCreateModal } from './ProyectoRiesgoCreateModal'
import ProyectoRiesgoListContext from './ProyectoRiesgoListContext'
import { RiesgoModel } from './RiesgoModel'

import { MenuItemHallazgo } from '../proyecto__hoja_trabajo/sidebar/menuitem_hallazgo'
import { MenuItemRevision } from '../proyecto__hoja_trabajo/sidebar/menuitem_revision'

import { HallazgoRiesgoModelManager, RevisionRiesgoModelManager } from './sidebar/menuitem__model_manager'

import cx from 'classnames'

declare var app: any

export class ProyectoRiesgoListSidebar extends PureComponent<{}, { panelOpen: string }> {
  state: { panelOpen: string } = {
    panelOpen: null
  }

  onItemOpen = (item: string) => {
    this.setState({ panelOpen: item })
  }

  closeAllPanel = () => {
    this.setState({ panelOpen: null })
  }

  render() {
    const editable = app.vars.proyecto.isOpen
    const url_base = `proyecto/${app.vars.idproyecto}/`
    const image_path = public_path('images/sidebar_right/')
    const counters = app.vars.riesgocontext.counters

    return (
      <Fragment>
        <div
          className={cx('sidebar-fixed-overlay ui page dimmer inverted', { active: this.state.panelOpen != null })}
          onClick={() => this.closeAllPanel()}
        />
        <Menu fixed='right' vertical compact icon className='app-sidebar right-sidebar' id='app-right-sidebar'>
          <Menu.Menu>
            <Menu.Item />
            {editable && <RiesgoMenuItemCreate />}
          </Menu.Menu>

          <Menu.Menu>
            <MenuItemHallazgo
              moduleParams={{ model: 'riesgo_hallazgo' }}
              counter={counters.hallazgos}
              open={this.state.panelOpen == 'hallazgo'}
              onOpen={this.onItemOpen}
              modelManager={HallazgoRiesgoModelManager.getInstance()}
            />
          </Menu.Menu>

          <Menu.Menu>
            <MenuItemRevision
              moduleParams={{ model: 'riesgo_revision' }}
              counter={counters.revisiones}
              open={this.state.panelOpen == 'revision'}
              onOpen={this.onItemOpen}
              modelManager={RevisionRiesgoModelManager.getInstance()}
            />
          </Menu.Menu>

          <Menu.Menu>
            <Menu.Item
              as='a'
              href={url_for(url_base + 'riesgos/export')}
              target='_blank'
              title='Exportar'
              content={<img src={image_path + 'exportar_excel.svg'} className='ui large image' />}
            />
          </Menu.Menu>

          <Menu.Menu>
            <Menu.Item
              as='a'
              href={url_for(url_base + 'riesgos/config')}
              title='Configuraciones'
              content={<img src={image_path + 'configuraciones.svg'} className='ui large image' />}
            />
          </Menu.Menu>
        </Menu>
      </Fragment>
    )
  }
}

class RiesgoMenuItemCreate extends PureComponent<{}, { open: boolean }> {
  static contextType = ProyectoRiesgoListContext

  state = {
    open: false
  }

  onClick = () => {
    this.setState({ open: true })
  }

  onClose = () => {
    this.setState({ open: false })
  }

  onCreate = (riesgo: Partial<ProyectoRiesgo> | { bibliotecaIds: string[] }) => {
    const { riesgos, updateRiesgos } = this.context

    if ('bibliotecaIds' in riesgo) {
      $.blockUI(create_blockUI_loading_message('Creando riesgos'))
      RiesgoModel.create(riesgo).then((riesgos_new: ProyectoRiesgo[]) => {
        const riesgos_copy = riesgos.slice()
        _.each(riesgos_new, (riesgo_new) => {
          riesgos_copy.push(riesgo_new)
        })
        this.setState({ open: false })
        updateRiesgos(riesgos_copy)
        $.unblockUI()
      })
    } else {
      $.blockUI(create_blockUI_loading_message('Creando riesgo'))
      RiesgoModel.create(riesgo).then((riesgo_new: ProyectoRiesgo) => {
        const riesgos_copy = riesgos.slice()
        riesgos_copy.push(riesgo_new)
        updateRiesgos(riesgos_copy)
        this.setState({ open: false })
        $.unblockUI()
      })
    }
  }

  render() {
    return (
      <Fragment>
        <Menu.Item
          link
          title='Crear Riesgo'
          className='item-create'
          onClick={this.onClick}
          content={<img src={public_path('images/sidebar_right/') + 'riesgos_new.svg'} className='ui large image' />}
        />
        {this.state.open && <ProyectoRiesgoCreateModal onClose={this.onClose} onCreate={this.onCreate} />}
      </Fragment>
    )
  }
}
