import { PureComponent, SyntheticEvent, Fragment } from 'react'

import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'
import Tab from 'semantic-ui-react/dist/commonjs/modules/Tab/Tab'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup/Popup'

import cx from 'classnames'

import EditorPreview from '../global/EditorPreview'
import { RiesgoFieldDropdown } from './ProyectoRiesgoUtils'
import ProyectoRiesgoListContext from './ProyectoRiesgoListContext'

declare var app: any

type ProyectoRiesgoCreateModalProps = {
  onCreate?: (riesgo: Partial<ProyectoRiesgo> | { bibliotecaIds: string[] }) => void
  onClose?: () => void
}

export class ProyectoRiesgoCreateModal extends PureComponent<ProyectoRiesgoCreateModalProps> {
  render() {
    const panes = [
      {
        menuItem: {
          key: 'library',
          className: 'tab-nav-item',
          content: <span className='tabname'>Desde Biblioteca</span>
        },
        render: () => (
          <Tab.Pane>
            <ProyectoRiesgoCreateFromLibrary onCancel={this.props.onClose} onCreate={this.props.onCreate} />
          </Tab.Pane>
        )
      },
      {
        menuItem: { key: 'new', className: 'tab-nav-item', content: <span className='tabname'>Nuevo</span> },
        render: () => (
          <Tab.Pane>
            <ProyectoRiesgoCreateNew onCancel={this.props.onClose} onCreate={this.props.onCreate} />
          </Tab.Pane>
        )
      }
    ]

    return (
      <Modal
        dimmer='inverted'
        open
        closeIcon
        closeOnDimmerClick={false}
        className='modal-ab'
        onClose={this.props.onClose}
      >
        <Modal.Header>Crear Riesgo</Modal.Header>
        <Modal.Content>
          <Tab panes={panes} menu={{ widths: 2, tabular: true, attached: 'top' }} className='ab-tabs' />
        </Modal.Content>
      </Modal>
    )
  }
}

type ProyectoRiesgoCreateNewProps = {
  onCreate?: (riesgo: Partial<ProyectoRiesgo> | { bibliotecaIds: string[] }) => void
  onCancel?: () => void
}

type ProyectoRiesgoCreateNewState = {
  codigo: string
  description: string
  ProyectoRiesgoHasRubro: { id: string }[]
  ProyectoRiesgoHasObjetivo: { id: string }[]
}

export class ProyectoRiesgoCreateNew extends PureComponent<ProyectoRiesgoCreateNewProps, ProyectoRiesgoCreateNewState> {
  state: ProyectoRiesgoCreateNewState = {
    codigo: '',
    description: '',
    ProyectoRiesgoHasRubro: [],
    ProyectoRiesgoHasObjetivo: []
  }

  onSubmit = () => {
    if (this.state.codigo == '' && this.state.description == '') {
      bootbox.alert('Ingrese el cdigo o la descripcin del riesgo')
      return
    }

    if ($.isFunction(this.props.onCreate)) {
      this.props.onCreate(this.state)
    }
  }

  render() {
    return (
      <div className='ui form'>
        <p className='description-text'>
          Diligencie los siguientes campos para crear un nuevo riesgo en este proyecto.
        </p>
        <div className='inline fields'>
          <div className='field'>
            <label htmlFor='proyecto_riesgo_new_codigo'>ID del Riesgo: </label>
            <div className='ui input'>
              <input
                id='proyecto_riesgo_new_codigo'
                type='text'
                value={this.state.codigo}
                placeholder='ID del Riesgo'
                onChange={(ev: SyntheticEvent) => this.setState({ codigo: _.trim((ev.target as any).value) })}
              />
            </div>
          </div>
        </div>
        <div className='fields'>
          <div className='field sixteen wide'>
            <div className='ui segment' style={{ minHeight: 90 }}>
              <EditorPreview
                data={this.state.description}
                className='riesgo-descripcion descripcion-field'
                ckeditorOptions='basic'
                prefixPreview='Descripcin del Riesgo: '
                labelTitle='Descripcin del Riesgo:'
                openOnClickPreview
                onSave={(editor: CKEDITOR.editor) => this.setState({ description: editor.getData() })}
              />
            </div>
          </div>
        </div>

        <div className='equal width fields'>
          <div className='field'>
            <label>Rubros</label>
            <div className='ui input'>
              <RiesgoFieldDropdown
                value={this.state.ProyectoRiesgoHasRubro}
                choices={app.vars.riesgocontext.rubros}
                field='id_master_data_fase'
                onChange={(value: any) => this.setState({ ProyectoRiesgoHasRubro: value })}
              />
            </div>
          </div>
          <div className='field'>
            <label>Aserciones</label>
            <div className='ui input'>
              <RiesgoFieldDropdown
                value={this.state.ProyectoRiesgoHasObjetivo}
                choices={app.vars.riesgocontext.objetivos}
                field='id_master_data_objetivo'
                onChange={(value: any) => this.setState({ ProyectoRiesgoHasObjetivo: value })}
              />
            </div>
          </div>
        </div>

        <div className='field'>
          <Popup
            disabled={this.state.codigo !== '' || this.state.description !== ''}
            content='Ingrese el cdigo o la descripcin del riesgo'
            header='Opcin deshabilitada'
            className='disabled-text'
            trigger={
              <div style={{ display: 'inline-block', float: 'right' }}>
                <button
                  type='button'
                  className='ui button right floated  primary'
                  disabled={this.state.codigo == '' && this.state.description == ''}
                  onClick={this.onSubmit}
                >
                  Crear Riesgo
                </button>
              </div>
            }
          />

          <button type='button' className='ui button right floated  negative' onClick={this.props.onCancel}>
            Cancelar
          </button>
          <div className='clearfix' />
        </div>
      </div>
    )
  }
}

type RiesgoGlobal = {
  id: string
  description: string
  rubro: string
  Objetivos: { id: string; description: string; name: string; code: string }[]
}

type ProyectoRiesgoCreateFromLibraryState = {
  riesgos: RiesgoGlobal[]
  selectedIds: string[]
}

export class ProyectoRiesgoCreateFromLibrary extends PureComponent<
  ProyectoRiesgoCreateNewProps,
  ProyectoRiesgoCreateFromLibraryState
> {
  static contextType = ProyectoRiesgoListContext
  state: ProyectoRiesgoCreateFromLibraryState = {
    riesgos: null,
    selectedIds: []
  }

  _isMounted = false

  componentDidMount() {
    this._isMounted = true
    if (app.vars.riesgocontext.biblioteca) {
      this.setState({ riesgos: app.vars.riesgocontext.biblioteca })
    } else {
      $.getJSON(url_for(`proyecto/${app.vars.idproyecto}/riesgos/list`), { type: 'biblioteca' }).done((res) => {
        app.vars.riesgocontext.biblioteca = res.response.riesgos
        if (this._isMounted) {
          this.setState({ riesgos: res.response.riesgos })
        }
      })
    }
  }

  componentWillUnmount() {
    this._isMounted = false
  }

  onChange = (ev: SyntheticEvent) => {
    const el = ev.target as any
    this.setState((state) => {
      let selectedIds = state.selectedIds.slice()
      if (_.includes(selectedIds, el.value)) {
        selectedIds = _.difference(selectedIds, [ el.value ])
      } else {
        selectedIds.push(el.value)
      }

      return { selectedIds }
    })
  }

  onSubmit = () => {
    if (this.state.selectedIds.length >= 0) {
      const { riesgos } = this.context
      const usedIds = _.pluck(riesgos, 'id_riesgo')

      const bibliotecaIds = _.difference(this.state.selectedIds, usedIds)
      this.props.onCreate({ bibliotecaIds })
      this.setState({ selectedIds: bibliotecaIds })
    }
  }

  render() {
    if (this.state.riesgos === null) {
      return (
        <div style={{ minHeight: 160 }}>
          <div className='ui active transition visible inverted dimmer'>
            <div className='ui inverted text loader'>Cargando Riesgos de la Biblioteca</div>
          </div>
        </div>
      )
    }

    const { riesgos } = this.context
    const usedIds = _.pluck(riesgos, 'id_riesgo')

    return (
      <Fragment>
        <div style={{ maxHeight: 420, overflowY: 'auto', marginBottom: 5 }}>
          <p>Seleccione los riesgos de la biblioteca que desea agregar a su proyecto.</p>
          <table className='ui table compact small striped'>
            <thead>
              <tr>
                <th />
                <th>Riesgo</th>
                <th>Rubro</th>
                <th>Aserciones</th>
              </tr>
            </thead>
            <tbody>
              {_.map(this.state.riesgos, (riesgo) => {
                return (
                  <tr key={riesgo.id} className={cx(_.include(usedIds, riesgo.id) ? 'success' : null)}>
                    <td>
                      {_.include(usedIds, riesgo.id) ? (
                        <i className='check icon green' />
                      ) : (
                        <input
                          type='checkbox'
                          name='biblioteca[id][]'
                          checked={_.include(this.state.selectedIds, riesgo.id)}
                          value={riesgo.id}
                          onChange={this.onChange}
                        />
                      )}
                    </td>
                    <td className='riesgo_descr'>{riesgo.description}</td>
                    <td>{riesgo.rubro}</td>
                    <td>
                      {_.map(riesgo.Objetivos, (objetivo) => {
                        return (
                          <Popup
                            key={objetivo.id}
                            trigger={
                              <span className='label label-success' style={{ marginRight: 2 }}>
                                {objetivo.code}
                              </span>
                            }
                            size='small'
                            header={`Objetivo: ${objetivo.name}`}
                            content={objetivo.description}
                          />
                        )
                      })}
                    </td>
                  </tr>
                )
              })}
            </tbody>
          </table>
        </div>

        <div className='field'>
          <button type='button' className='ui button right floated  primary' onClick={this.onSubmit}>
            Crear Riesgos
          </button>
          <button type='button' className='ui button right floated  negative' onClick={this.props.onCancel}>
            Cancelar
          </button>
          <div className='clearfix' />
        </div>
      </Fragment>
    )
  }
}
