import Tab from 'semantic-ui-react/dist/commonjs/modules/Tab/Tab'
import Menu from 'semantic-ui-react/dist/commonjs/collections/Menu/Menu'

//import TabProcesos from './TabProcesos'
import TabRiesgos from './TabRiesgos'
//import TabRiesgosMapa from './TabRiesgosMapa'
import { SyntheticEvent } from 'react'

const TABS = [
  //{ key: 'procesos', name: 'Procesos', pane: <TabProcesos /> },
  { key: 'riesgos', name: 'Riesgos', pane: <TabRiesgos /> }
  //{ key: 'riesgosmap', name: 'Mapa de Riesgos', pane: <TabRiesgosMapa /> }
]

const App = () => {
  const panes = _.map(TABS, (tab, index) => {
    return {
      key: tab.name,
      menuItem: (
        <Menu.Item
          className='tab-nav-item'
          content={<span className='tabname'>{tab.name}</span>}
          key={`${tab.key}-tab`}
          index={index}
          as='a'
          style={{ width: '99%' }}
        />
      ),
      render: () => tab.pane
    }
  })

  const onTabChange = (ev: SyntheticEvent, { activeIndex }: { activeIndex: number }) => {
    window.location.hash = '#!' + TABS[activeIndex].key
  }

  const defaultActiveIndex = (() => {
    const hash = window.location.hash.replace(/^#!/, '').split('/')[0]
    if (hash == '') {
      return 0
    }
    const tab = _.findIndex(TABS, { key: hash })

    return tab == -1 ? 0 : tab
  })()

  return (
    <div id='app-tabs' className='app-tabs'>
      <Tab panes={panes} defaultActiveIndex={defaultActiveIndex} renderActiveOnly onTabChange={onTabChange} />
    </div>
  )
}

export default App
