import { PureComponent, SyntheticEvent, Fragment } from 'react'

import Table from 'semantic-ui-react/dist/commonjs/collections/Table/Table'
import Pagination from 'semantic-ui-react/dist/commonjs/addons/Pagination/Pagination'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup/Popup'

import EditorPreview from '../global/EditorPreview'
import { DefaultErrorHandler } from '../global/ErrorHandler'
import ProyectoRiesgoListContext from './ProyectoRiesgoListContext'

import cx from 'classnames'

import ProyectoRiesgoModal from './ProyectoRiesgoModal'
import { ProyectoRiesgoListSidebar } from './ProyectoRiesgoListSidebar'
import { createRiesgoCellBlock } from './ProyectoRiesgoUtils'
import { RiesgoModel } from './RiesgoModel'

declare var app: any

type TabRiesgosState = {
  riesgos: ProyectoRiesgo[]
}

export default class TabRiesgos extends PureComponent<{}, TabRiesgosState> {
  state: TabRiesgosState = {
    riesgos: null
  }

  componentDidMount() {
    $.getJSON(url_for(`proyecto/${app.vars.idproyecto}/riesgos/list`)).done((res) => {
      this.setState({ riesgos: res.response.riesgos })
    })

    $.getJSON(url_for('proyecto/' + app.vars.idproyecto + '/master_data_tree'), {
      export_options: { type: 'pasolink' }
    }).done((res) => {
      app.vars.masterdatatree = res.response

      app.vars.masterdatapasos = {}
      _.each(res.response, (fase) => {
        _.each(fase.Subfases, (subfase) => {
          _.each(subfase.Pasos, (paso) => {
            app.vars.masterdatapasos[paso.id] = paso
          })
        })
      })
    })
  }

  updateRiesgos = (riesgos: ProyectoRiesgo[]) => {
    this.setState({ riesgos })
  }

  render() {
    return (
      <ProyectoRiesgoListContext.Provider value={{ riesgos: this.state.riesgos, updateRiesgos: this.updateRiesgos }}>
        <div id='tablelistriesgo_wrapper'>
          <TableRiesgoList />
        </div>
        <DefaultErrorHandler>
          <ProyectoRiesgoListSidebar />
        </DefaultErrorHandler>
      </ProyectoRiesgoListContext.Provider>
    )
  }
}

type TableRiesgoListState = {
  currentPage: number
  filters: TableRowFiltersState
}

class TableRiesgoList extends PureComponent<{}, TableRiesgoListState> {
  static contextType = ProyectoRiesgoListContext

  state: TableRiesgoListState = {
    currentPage: 1,
    filters: null
  }

  TOTAL_PER_PAGE = 25

  onPageChange = (ev: SyntheticEvent, data: any) => {
    this.setState({ currentPage: data.activePage })
  }

  getFilteredList(riesgos: ProyectoRiesgo[]) {
    const filters = this.state.filters
    if (!_.size(filters)) {
      return riesgos
    }

    const choiceFilterMatches = (filter: string, value: string): boolean => {
      if (typeof filter === 'undefined') {
        return true
      }

      if (filter == '-1') {
        return value == 'null' || value == ''
      }

      if (filter.substr(0, 1) == '!') {
        return value !== filter.substr(1)
      }

      return '' + filter === '' + value
    }

    const textFilterMatches = (filter: string, value: string): boolean => {
      if (typeof filter === 'undefined') {
        return true
      }

      return value.toUpperCase().includes(filter.trim().toUpperCase())
    }

    let filtered: ProyectoRiesgo[] = []
    _.each(riesgos, (riesgo) => {
      if (!choiceFilterMatches(filters.rf, riesgo.fraude_material ? '1' : '0')) {
        return
      }

      if (!choiceFilterMatches(filters.ri, '' + riesgo.riesgo_inherente)) {
        return
      }

      if (!choiceFilterMatches(filters.ec, '' + riesgo.control_valoracion)) {
        return
      }

      if (!choiceFilterMatches(filters.vc, '' + riesgo.control_efectividad)) {
        return
      }

      if (!choiceFilterMatches(filters.rr, '' + riesgo.riesgo_residual)) {
        return
      }

      if (!textFilterMatches(filters.desc, riesgo.codigo) && !textFilterMatches(filters.desc, riesgo.description)) {
        return
      }

      filtered.push(riesgo)
    })

    return filtered
  }

  getPagedList(riesgos: ProyectoRiesgo[]) {
    if (riesgos === null) {
      return { riesgos: null, paginationInfo: null }
    }

    const filteredRiesgos = this.getFilteredList(riesgos)
    const paged = filteredRiesgos.slice(
      (this.state.currentPage - 1) * this.TOTAL_PER_PAGE,
      this.state.currentPage * this.TOTAL_PER_PAGE
    )

    return {
      riesgos: paged,
      paginationInfo: {
        start: (this.state.currentPage - 1) * this.TOTAL_PER_PAGE + 1,
        end: Math.min(this.state.currentPage * this.TOTAL_PER_PAGE, filteredRiesgos.length),
        total: filteredRiesgos.length
      }
    }
  }

  componentDidMount() {
    $('#tableriesgolist').floatThead()
  }

  componentWillUnmount() {
    $('#tableriesgolist').floatThead('destroy')
  }

  componentDidUpdate() {
    setTimeout(function() {
      $('#tableriesgolist').floatThead('reflow')
    }, 100)
  }

  render() {
    const headers = [
      'Fraude',
      'Riesgo',
      'Riesgo Inherente',
      'Evaluacin de Control',
      'Validacin de Control',
      'Riesgo Residual',
      ''
    ]

    const { riesgos } = this.context
    const { riesgos: pagedRiesgos, paginationInfo } = this.getPagedList(riesgos)

    return (
      <Table id='tableriesgolist' className='tableriesgolist' basic='very' compact>
        <Table.Header>
          <tr>
            {_.map(headers, (header, index) => {
              return <Table.HeaderCell collapsing={index == 0} key={index} className={`th-${index}`} content={header} />
            })}
          </tr>
        </Table.Header>
        <Table.Body>
          {riesgos === null ? (
            <tr>
              <td colSpan={headers.length}>
                <div style={{ height: 300, position: 'relative' }}>
                  <div className='ui active inverted text loader elastic olive'>Cargando Listado de Riesgos</div>
                </div>
              </td>
            </tr>
          ) : (
            <Fragment>
              <TableRowFilters onChange={(filters: any) => this.setState({ filters })} />
              {pagedRiesgos.length > 0 ? (
                _.map(pagedRiesgos, (riesgo: ProyectoRiesgo) => {
                  return <TableRowRiesgo riesgo={riesgo} key={riesgo.id} />
                })
              ) : (
                <tr>
                  <td colSpan={headers.length}>
                    <div className='nocontent empty' style={{ margin: '7% auto' }}>
                      0 Riesgos Encontrados
                    </div>
                  </td>
                </tr>
              )}
            </Fragment>
          )}
        </Table.Body>
        <Table.Footer>
          <tr>
            <td colSpan={headers.length}>
              {riesgos !== null &&
              paginationInfo.total > this.TOTAL_PER_PAGE && (
                <div className='pull-left pagination-wrapper'>
                  <Pagination
                    activePage={this.state.currentPage}
                    boundaryRange={2}
                    totalPages={Math.ceil(paginationInfo.total / this.TOTAL_PER_PAGE)}
                    size='mini'
                    onPageChange={this.onPageChange}
                  />
                </div>
              )}
              {riesgos !== null && (
                <div className='pull-right pagination-info'>
                  {paginationInfo.start} &mdash; {paginationInfo.end} / {paginationInfo.total} Riesgos
                  {_.size(this.state.filters) > 0 && <span className='filtered-label'> (FILTRADOS)</span>}
                </div>
              )}
            </td>
          </tr>
        </Table.Footer>
      </Table>
    )
  }
}

type TableRowFiltersState = {
  desc: string
  rf: string
  ri: string
  ec: string
  vc: string
  rr: string
}

class TableRowFilters extends PureComponent<{ onChange: (filters: any) => void }, TableRowFiltersState> {
  state: TableRowFiltersState = {
    desc: '',
    rf: '',
    ri: '',
    ec: '',
    vc: '',
    rr: ''
  }

  onChange(field: keyof TableRowFiltersState, value: string) {
    const newstate: any = { [field]: value }
    this.setState(newstate, () => {
      this.props.onChange(this.getActiveFilters())
    })
  }

  getActiveFilters() {
    let filters: any = {}
    _.each(this.state, (value, key) => {
      if (value !== '') {
        filters[key] = value
      }
    })

    return filters
  }

  onClickReset = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.setState(
      {
        desc: '',
        rf: '',
        ri: '',
        ec: '',
        vc: '',
        rr: ''
      },
      () => {
        this.props.onChange(this.getActiveFilters())
      }
    )
  }

  render() {
    return (
      <Fragment>
        <tr className='filters-row'>
          <td className='collapsing'>
            <select
              className={cx('rawselect rf', this.state.rf !== '' ? 'active' : null)}
              value={this.state.rf}
              onChange={(ev: SyntheticEvent) => this.onChange('rf', (ev.target as any).value)}
            >
              <option value=''>TODOS</option>
              <option value='1'>SI</option>
              <option value='0'>NO</option>
            </select>
          </td>
          <td>
            <input
              type='search'
              spellCheck={false}
              className={cx(this.state.desc !== '' ? 'active' : null)}
              value={this.state.desc}
              onChange={(ev: SyntheticEvent) => this.onChange('desc', (ev.target as any).value)}
            />
          </td>
          <td>
            <select
              className={cx('rawselect', this.state.ri !== '' ? 'active' : null)}
              value={this.state.ri}
              onChange={(ev: SyntheticEvent) => this.onChange('ri', (ev.target as any).value)}
            >
              <option value=''>TODOS</option>
              <option value='5'>Alto</option>
              <option value='3'>Medio</option>
              <option value='1'>Bajo</option>
              <option disabled>-</option>
              <option value='!1'>Alto y Medio</option>
              <option value='!3'>Alto y Bajo</option>
              <option value='!5'>Medio y Bajo</option>
              <option disabled>-</option>
              <option value='-1'>Por Definir</option>
            </select>
          </td>
          <td>
            <select
              className={cx('rawselect', this.state.ec !== '' ? 'active' : null)}
              value={this.state.ec}
              onChange={(ev: SyntheticEvent) => this.onChange('ec', (ev.target as any).value)}
            >
              <option value=''>TODOS</option>
              <option value='5'>Fuerte</option>
              <option value='3'>Moderado</option>
              <option value='1'>Dbil</option>
              <option disabled>-</option>
              <option value='!1'>Fuerte y Moderado</option>
              <option value='!3'>Fuerte y Dbil</option>
              <option value='!5'>Moderado y Dbil</option>
              <option disabled>-</option>
              <option value='-1'>Por Definir</option>
            </select>
          </td>
          <td>
            <select
              className={cx('rawselect', this.state.vc !== '' ? 'active' : null)}
              value={this.state.vc}
              onChange={(ev: SyntheticEvent) => this.onChange('vc', (ev.target as any).value)}
            >
              <option value=''>TODOS</option>
              <option value='5'>Efectivo</option>
              <option value='3'>Con Deficiencias</option>
              <option value='1'>Inefectivo</option>
              <option disabled>-</option>
              <option value='!1'>Efectivo o Con Deficiencias</option>
              <option value='!3'>Efectivo o Inefectivo</option>
              <option value='!5'>Con Deficiencias o Inefectivo</option>
              <option disabled>-</option>
              <option value='-1'>Por Definir</option>
            </select>
          </td>
          <td>
            <select
              className={cx('rawselect', this.state.rr !== '' ? 'active' : null)}
              value={this.state.rr}
              onChange={(ev: SyntheticEvent) => this.onChange('rr', (ev.target as any).value)}
            >
              <option value=''>TODOS</option>
              <option value='5'>Alto</option>
              <option value='3'>Medio</option>
              <option value='1'>Bajo</option>
              <option disabled>-</option>
              <option value='!1'>Alto y Medio</option>
              <option value='!3'>Alto y Bajo</option>
              <option value='!5'>Medio y Bajo</option>
              <option disabled>-</option>
              <option value='-1'>Por Definir</option>
            </select>
          </td>
        </tr>
        {_.size(this.getActiveFilters()) > 0 && (
          <tr>
            <td colSpan={7}>
              <div id='filteralerts' className='alert-2 alert-primary'>
                <span className='icon-info-sign' style={{ marginRight: 3 }} />
                NOTA: La visibilidad de los riesgos est afectada por los filtros aplicados.
                <a href='#' className='resetfilters' style={{ marginLeft: 10 }} onClick={this.onClickReset}>
                  Quitar filtros
                </a>
              </div>
            </td>
          </tr>
        )}
      </Fragment>
    )
  }
}

class TableRowRiesgo extends PureComponent<{ riesgo: ProyectoRiesgo }, { openModal: boolean; loading: boolean }> {
  static contextType = ProyectoRiesgoListContext
  state = {
    openModal: false,
    loading: false
  }

  componentDidMount() {
    let changes = RiesgoModel.setChanges(this.props.riesgo, {})
    if (changes) {
      this.setState({ loading: true })
      const { riesgos, updateRiesgos } = this.context
      RiesgoModel.save(this.props.riesgo, changes).then(() => {
        let riesgosCopy = _.map(riesgos, (riesgo: ProyectoRiesgo) => {
          if (riesgo.id == this.props.riesgo.id) {
            return Object.assign({}, riesgo, changes)
          } else {
            return riesgo
          }
        })
        this.setState({ loading: false })
        updateRiesgos(riesgosCopy)
      })
    }
  }

  onEditDescription = (editor: any) => {
    editor.getData()
  }

  getLabel(input: number, cases: any) {
    if (!input) {
      return 'Por Definir'
    }

    return cases[input]
  }

  onClickEdit = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.setState({ openModal: true })
  }

  onCloseModal = () => {
    this.setState({ openModal: false })
  }

  getRiesgo(): ProyectoRiesgo {
    return _.find(this.context.riesgos, { id: this.props.riesgo.id })
  }

  render() {
    const { riesgo } = this.props
    const { loading } = this.state

    const descriptionPreview =
      '<strong class="riesgo-code">' + riesgo.codigo + '</strong> - ' + (riesgo.description || '')

    const is_editable = app.vars.proyecto.isOpen

    return (
      <tr className='hoverable-actions'>
        <td className='collapsing center aligned' style={{ padding: '0.5em 0' }}>
          {+riesgo.fraude_material == 1 && (
            <Popup
              trigger={<i className='red exclamation circle large icon' aria-hidden='true' />}
              content='Riesgo de Fraude'
              size='mini'
            />
          )}
        </td>
        <td className='table-cell-riesgo-description'>
          <div className='ui compact segment' onClick={this.onClickEdit}>
            <EditorPreview preview={descriptionPreview} data={riesgo.description} editable={false} />
          </div>
        </td>
        <td>{createRiesgoCellBlock(riesgo, 'riesgo_inherente', { onClick: this.onClickEdit, loading })}</td>
        <td>{createRiesgoCellBlock(riesgo, 'control_valoracion', { onClick: this.onClickEdit, loading })}</td>
        <td>{createRiesgoCellBlock(riesgo, 'control_efectividad', { onClick: this.onClickEdit, loading })}</td>
        <td>{createRiesgoCellBlock(riesgo, 'riesgo_residual', { onClick: this.onClickEdit, loading })}</td>
        <td>
          {is_editable && (
            <div className='btn-group dropdown-modern'>
              <a className='dropdown-toggle' data-toggle='dropdown' href='#'>
                <i className='ellipsis vertical grey icon' />
              </a>

              <ul className='dropdown-menu pull-right'>
                {user_has_role_permission('riesgo_edit') && (
                  <li>
                    <a href='#' onClick={this.onClickEdit}>
                      Editar Riesgo
                    </a>
                  </li>
                )}
                {user_has_role_permission('riesgo_delete') && <RiesgoDropdownDeleteAction riesgo={riesgo} />}
              </ul>
            </div>
          )}
          {this.state.openModal && (
            <ProyectoRiesgoModal
              riesgo={riesgo}
              onClose={this.onCloseModal}
              editable={is_editable && user_has_role_permission('riesgos_edit')}
            />
          )}
        </td>
      </tr>
    )
  }
}

class RiesgoDropdownDeleteAction extends PureComponent<{ riesgo: ProyectoRiesgo }> {
  static contextType = ProyectoRiesgoListContext

  onClick = (ev: SyntheticEvent) => {
    ev.preventDefault()

    const { riesgo } = this.props
    const { riesgos, updateRiesgos } = this.context

    bootbox.warningConfirm(
      `Esta seguro de querer eliminar por completo el riesgo "<strong>${riesgo.codigo}</strong>"?`,
      'No',
      'S, Eliminar Riesgo',
      (answer) => {
        if (!answer) {
          return
        }

        $.blockUI(create_blockUI_loading_message('Eliminando Riesgo...'))
        $.post(url_for(`proyecto/${app.vars.idproyecto}/riesgos/${riesgo.id}`), { sf_method: 'delete' })
          .done(() => {
            updateRiesgos(_.reject(riesgos, { id: riesgo.id }))
            $.unblockUI()
          })
          .fail(() => $.unblockUI())
      }
    )
  }

  render() {
    return (
      <li>
        <a href='#' onClick={this.onClick}>
          Eliminar Riesgo
        </a>
      </li>
    )
  }
}
