/// <reference path="index.d.ts" />

import { PureComponent, Fragment, useState } from 'react'

import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'
import { instance as eventStack } from '@semantic-ui-react/event-stack'

import Slider from 'react-rangeslider'
import keyboardKey from 'keyboard-key'

import ProyectoRiesgoListContext from '../proyecto__riesgo/ProyectoRiesgoListContext'
import EditorPreview from '../global/EditorPreview'
import ButtonEdit from '../global/ButtonEdit'
import { GenericTooltip } from '../global/Tooltip'
import { PasoSelect } from '../global/PasoSelect'

import {
  createRiesgoCellBlock,
  RiesgoFieldDropdown,
  RiesgoCriterioEvaluacionControlTable
} from '../proyecto__riesgo/ProyectoRiesgoUtils'
import { RiesgoModel } from './RiesgoModel'

import cx from 'classnames'

declare var app: any

type ProyectoRiesgoModalProps = {
  riesgo: ProyectoRiesgo
  editable?: boolean
  onUpdate?: (riesgo: ProyectoRiesgo) => void
  onClose?: () => void
}

type ProyectoRiesgoFields = { [P in keyof ProyectoRiesgo]?: ProyectoRiesgo[P] }

const createTooltip = (text: string, tooltip: string, title: string = null) => {
  return (
    <Fragment>
      {text} <GenericTooltip text={tooltip} title={title || text} />
    </Fragment>
  )
}

export default class ProyectoRiesgoModal extends PureComponent<ProyectoRiesgoModalProps> {
  static contextType = ProyectoRiesgoListContext

  static defaultProps = {
    editable: true
  }

  private handlers: { [s: string]: any } = {}

  /**
   * Send changes to server
   *
   * @param changes Changes to send to server
   * @param silent If false, shows loading indicator.
   */
  sendToServer(changes: ProyectoRiesgoFields, silent: boolean = false) {
    const { riesgo }: { riesgo: ProyectoRiesgo } = this.props

    changes = RiesgoModel.setChanges(riesgo, changes)

    if (!changes) {
      return
    }

    if (!silent) {
      $.blockUI(create_blockUI_loading_message('Guardando Cambios...'))
    } else {
      this.updateModel(changes)
    }

    RiesgoModel.save(riesgo, changes)
      .then(() => {
        if (!silent) {
          this.updateModel(changes)
          $.unblockUI()
        }
      })
      .catch((error) => {
        bootbox.dialog(error, [ { label: 'OK', class: 'btn-danger' } ], {
          header: '<span class="icon-warning-sign"></span> Error de Validacin - Dato no vlido',
          classes: 'bootbox-error'
        })
        $.unblockUI()
      })
  }

  updateModel(changes: ProyectoRiesgoFields) {
    const { riesgo } = this.props
    const { riesgos, updateRiesgos } = this.context

    const _riesgos: ProyectoRiesgo[] = _.map(riesgos, (_riesgo: ProyectoRiesgo) => {
      if (_riesgo.id == riesgo.id) {
        _riesgo = Object.assign({}, _riesgo, changes)
      }
      return _riesgo
    })

    updateRiesgos(_riesgos)
  }

  attachHandler(field: string, handler: any) {
    if (!this.handlers[field]) {
      this.handlers[field] = handler
    }

    return this.handlers[field]
  }

  handleEscape = (e: Event) => {
    if (keyboardKey.getCode(e) !== keyboardKey.Escape) return

    if (typeof CKEDITOR !== 'undefined' && CKEDITOR.dialog.getCurrent() != null) {
      // si existe un CKEditor dialog abierto, no cerrar el modal.
      return
    }

    if ($('.bootbox:visible').length > 0) {
      // si existe un cuadro de dialog abierto, no cerrar el modal.
      return
    }

    this.onClose()
  }

  onClose = () => {
    this.props.onClose()
  }

  onModalMount = () => {
    eventStack.sub('keydown', this.handleEscape, { pool: 'default' })
  }

  onClickEditCodigo = (field: 'codigo' | 'control_codigo') => {
    return this.attachHandler(field, () => {
      const { riesgo }: { riesgo: ProyectoRiesgo } = this.props

      const promptMessage = (() => {
        switch (field) {
          case 'codigo':
            return { content: 'Escriba el nuevo Cdigo del Riesgo.', header: 'Cdigo del Riesgo' }
          case 'control_codigo':
            return { content: 'Escriba el nuevo Cdigo del Control.', header: 'Cdigo del Control' }
        }
      })()

      const defaultValue = riesgo[field] || ''
      bootbox.prompt(
        promptMessage,
        'Cancelar',
        'Aceptar',
        (res: string) => {
          res = _.str.trim(res)
          if (res == '') {
            return
          }

          this.sendToServer({ [field]: res })
        },
        defaultValue
      )

      $('.bootbox.modal').addClass('modal-ab')
    })
  }

  onClickEditDescription = (field: string) => {
    const handler = (editor: CKEDITOR.editor, closeModal: boolean, autosave: boolean, cb: () => void) => {
      this.sendToServer({ [field]: editor.getData() })
      cb()
    }

    return this.attachHandler(field, handler)
  }

  onChangeControlCodigo = (value: string) => {
    this.sendToServer({ control_codigo: value }, true)
  }

  onClickRemoveControlCodigo = () => {
    bootbox.warningConfirm(
      'Desea eliminar este control?<br/>Tenga en cuenta que esto eliminar TODA la informacin (Cdigo, Descripcin, Valoracin, Evaluaciones y Validaciones) registrada en este control',
      'No',
      'S, eliminar control',
      (answer) => {
        if (answer) {
          let changes = RiesgoModel.getChangesWhenRemoveControlCodigo(this.props.riesgo)
          this.sendToServer(changes, true)
        }
      }
    )
  }

  onChangeRiesgoFraude = (checked: boolean) => {
    this.sendToServer({ fraude_material: checked ? 1 : 0 }, true)
  }

  onChangeSlider = (field: string) => {
    return this.attachHandler(field, (value: any) => {
      value = value * 2 - 1
      let changes = { [field]: value }

      if (field == 'probabilidad' || field == 'impacto') {
        // Si el campo es probabilidad o impacto, recalcular el riesgo inherente
        const riesgo_inherente = this.calcularRiesgoInherente(changes)
        if (riesgo_inherente) {
          changes.riesgo_inherente = riesgo_inherente
        }
      }
      this.sendToServer(changes, true)
    })
  }

  onChangeDropdown = (field: string) => {
    return this.attachHandler(field, (value: any) => {
      let changes = { [field]: value }
      this.sendToServer(changes, true)
    })
  }

  onChangeCriterioEvaluacion = (field: string, { value }: { value: string }) => {
    let changes: any = { [field]: value }
    this.sendToServer(changes, true)
  }

  onSavePaso = (selectedIds: string[]) => {
    const ids = _.map(selectedIds, (id) => {
      return { id_master_data_paso: id, id: id }
    })

    this.sendToServer({ ProyectoRiesgoHasPaso: ids })
  }

  cloneRiesgoWithChanges(changes: any): ProyectoRiesgo {
    const { riesgo } = this.props

    return Object.assign({}, riesgo, changes || {})
  }

  calcularRiesgoInherente(changes: any) {
    return RiesgoModel.calculateRiesgoInherente(this.cloneRiesgoWithChanges(changes))
  }

  renderSection1() {
    const { riesgo, editable } = this.props

    const header = (
      <div className='row ui middle aligned  grid '>
        <div className='four wide column'>
          <strong>Riesgo: </strong>
          <span>{riesgo.codigo}</span>
        </div>
        <div className='nine wide column right aligned'>
          <strong>Riesgo Inherente: </strong>
          {createRiesgoCellBlock(riesgo, 'riesgo_inherente')}
        </div>
        <div className='three wide column right aligned'>
          {+riesgo.fraude_material == 1 && (
            <strong>
              Fraude <i className='red exclamation circle large icon' aria-hidden='true' />
            </strong>
          )}
        </div>
      </div>
    )

    const content = (
      <Fragment>
        <div className='row'>
          <div className='eight wide column'>
            <strong>ID del Riesgo: </strong>
            <span>{riesgo.codigo}</span>
            {editable && <ButtonEdit onClick={this.onClickEditCodigo('codigo')} />}
          </div>
        </div>
        <div className='row'>
          <div className='sixteen wide column'>
            <div className='ui segment'>
              <EditorPreview
                className='riesgo-descripcion descripcion-field'
                data={riesgo.description}
                editable={editable}
                ckeditorOptions='basic'
                prefixPreview='Descripcin del Riesgo: '
                labelTitle='Descripcin del Riesgo:'
                openOnClickPreview={editable}
                onSave={this.onClickEditDescription('description')}
              />
            </div>
          </div>
        </div>

        <div className='three column row'>
          <div className='column field _a-center'>
            <header>
              <strong>Rubros</strong>
            </header>
            <RiesgoFieldDropdown
              value={riesgo.ProyectoRiesgoHasRubro}
              choices={app.vars.riesgocontext.rubros}
              field='id_master_data_fase'
              onChange={this.onChangeDropdown('ProyectoRiesgoHasRubro')}
              editable={editable}
            />
          </div>
          <div className='column field _a-center'>
            <header>
              <strong>Aserciones</strong>
            </header>
            <RiesgoFieldDropdown
              value={riesgo.ProyectoRiesgoHasObjetivo}
              choices={app.vars.riesgocontext.objetivos}
              field='id_master_data_objetivo'
              onChange={this.onChangeDropdown('ProyectoRiesgoHasObjetivo')}
              editable={editable}
            />
          </div>
        </div>

        <div className='three column  row n1'>
          <div className='column field _a-center'>
            <header>
              <div className='title'>
                Probabilidad <GenericTooltip text={'@tooltip.riesgos.probabilidad'} title='Probabilidad' />
              </div>
              <div className='bordertopline' />
            </header>
            <RiesgoFieldSlider
              value={riesgo.probabilidad}
              labels={{
                1: createTooltip('Bajo', '@tooltip.riesgos.probabilidad__bajo', 'Probabilidad Baja'),
                2: createTooltip('Medio', '@tooltip.riesgos.probabilidad__medio', 'Probabilidad Medio'),
                3: createTooltip('Alto', '@tooltip.riesgos.probabilidad__alto', 'Probabilidad Alta')
              }}
              editable={editable}
              onChange={this.onChangeSlider('probabilidad')}
            />
          </div>
          <div className='column field _a-center'>
            <header>
              <div className='title'>
                Impacto <GenericTooltip text={'@tooltip.riesgos.impacto'} title='Impacto' />
              </div>
              <div className='bordertopline' />
            </header>
            <RiesgoFieldSlider
              value={riesgo.impacto}
              labels={{ 1: 'Bajo', 2: 'Medio', 3: 'Alto' }}
              editable={editable}
              onChange={this.onChangeSlider('impacto')}
            />
          </div>
          <div className='column field _a-center'>
            <header>
              <div className='title'>
                Riesgo Inherente
                <GenericTooltip text={'@tooltip.riesgos.riesgo_inherente'} title='Riesgo Inherente' />
              </div>
              <div className='bordertopline' />
            </header>
            <BMACheckboxGroup value={riesgo.riesgo_inherente} />
          </div>
        </div>

        <div className='row'>
          <div className='sixteen wide column right aligned'>
            <div className='zone-g'>
              <strong>Este riesgo tiene probabilidad de un fraude material?</strong>
              <RiesgoFraudeMaterialCheckboxGroup
                editable={editable}
                checked={riesgo.fraude_material == 1}
                onChange={this.onChangeRiesgoFraude}
              />
            </div>
          </div>
        </div>
      </Fragment>
    )

    return <ProyectoRiesgoModalSection header={header} content={content} />
  }

  renderSection2() {
    const { riesgo, editable } = this.props

    const header = (
      <div className='row ui middle aligned  grid '>
        <div className='six wide column'>
          <strong>
            Control: <GenericTooltip text={'@tooltip.riesgos.control'} title='Control del Riesgo' />{' '}
          </strong>
          <span>{riesgo.control_codigo}</span>
        </div>
        <div className='nine wide column right aligned'>
          <strong>
            Evaluacin del Control:
            <GenericTooltip text={'@tooltip.riesgos.control_valoracion'} title='Evaluacin del Control' />
          </strong>
          {createRiesgoCellBlock(riesgo, 'control_valoracion')}
        </div>
      </div>
    )

    const content = (
      <Fragment>
        <div className='row'>
          <div className='eight wide column'>
            <strong>ID del Control: </strong>
            <RiesgoControlCodigoInput value={riesgo.control_codigo} onChange={this.onChangeControlCodigo} />
            {editable && riesgo.control_codigo && <ButtonEdit onClick={this.onClickEditCodigo('control_codigo')} />}
            {editable &&
            riesgo.control_codigo && (
              <button
                className='ui button mini negative compact icon basic action'
                title='Eliminar Control'
                onClick={this.onClickRemoveControlCodigo}
              >
                <i className='trash icon' />
              </button>
            )}
          </div>
        </div>
        <div className='row'>
          <div className='sixteen wide column'>
            <div className='ui segment'>
              <EditorPreview
                className='control-descripcion  descripcion-field'
                data={riesgo.control_descripcion}
                editable={editable}
                ckeditorOptions='basic'
                prefixPreview='Descripcin del Control: '
                labelTitle='Descripcin del Control:'
                openOnClickPreview={editable}
                onSave={this.onClickEditDescription('control_descripcion')}
              />
            </div>
          </div>
        </div>
        <div className='row'>
          <div className='sixteen wide column'>
            <div className={cx('ui segment nopad noborder', !riesgo.control_codigo ? 'dimmable' : null)}>
              <RiesgoCriterioEvaluacionControlTable
                riesgo={riesgo}
                onChange={this.onChangeCriterioEvaluacion}
                editable={editable}
              />
              <RiesgoControlDimmer riesgo={riesgo} editable={editable} />
            </div>
          </div>
        </div>
      </Fragment>
    )

    return <ProyectoRiesgoModalSection header={header} content={content} />
  }

  renderSection3() {
    const { riesgo, editable } = this.props

    const header = (
      <div className='row ui middle aligned  grid '>
        <div className='six wide column'>
          <strong>Validacin del Control: </strong>
        </div>
        <div className='nine wide column right aligned'>
          <strong>Riesgo Residual: </strong>
          {createRiesgoCellBlock(riesgo, 'riesgo_residual')}
        </div>
      </div>
    )

    const labels = {
      efectividad: {
        1: createTooltip('Inefectivo', '@tooltip.riesgos.control_efectividad__inefectivo'),
        2: createTooltip('Con Deficiencias', '@tooltip.riesgos.control_efectividad__condeficiencias'),
        3: createTooltip('Efectivo', '@tooltip.riesgos.control_efectividad__efectivo')
      }
    }

    const content = (
      <Fragment>
        <div className='row'>
          <div className='sixteen wide column'>
            <div className={cx('ui segment', !!riesgo.control_codigo || 'dimmable')}>
              <EditorPreview
                className='control-validacion-control  descripcion-field'
                data={riesgo.control_debilidad_descripcion}
                editable={editable}
                prefixPreview='Validacin del Control: '
                labelTitle='Validacin del Control:'
                openOnClickPreview={editable}
                onSave={this.onClickEditDescription('control_debilidad_descripcion')}
                ckeditorUploadOptions={{ model: 'riesgo__control_validacion', modelId: riesgo.id }}
                footer='AttachmentList'
              />

              <br />
              <PasoAssignModal riesgo={riesgo} editable={editable} onSave={this.onSavePaso} />
              <RiesgoControlDimmer riesgo={riesgo} editable={editable} />
            </div>
          </div>
        </div>
        <div className='two column  row n1'>
          <div className={cx('column field _a-center', !!riesgo.control_codigo || 'dimmable')}>
            <header>
              <div className='title'>
                Efectividad del Control
                <GenericTooltip text={'@tooltip.riesgos.control_efectividad'} title='Efectividad del Control' />
              </div>
              <div className='bordertopline' />
            </header>
            <RiesgoFieldSlider
              value={riesgo.control_efectividad}
              labels={labels.efectividad}
              onChange={this.onChangeSlider('control_efectividad')}
              editable={editable}
              inverted
            />
            <RiesgoControlDimmer riesgo={riesgo} editable={editable} />
          </div>
          <div className='column field _a-center'>
            <header>
              <div className='title'>
                Riesgo Residual
                <GenericTooltip text={'@tooltip.riesgos.riesgo_residual'} title='Riesgo Residual' />
              </div>
              <div className='bordertopline' />
            </header>
            <BMACheckboxGroup
              value={riesgo.riesgo_residual}
              tooltips={{
                1: <GenericTooltip text='@tooltip.riesgos.riesgo_residual__bajo' title='Riesgo Residual - Bajo' />,
                3: <GenericTooltip text='@tooltip.riesgos.riesgo_residual__moderado' title='Riesgo Residual - Medio' />,
                5: <GenericTooltip text='@tooltip.riesgos.riesgo_residual__alto' title='Riesgo Residual - Alto' />
              }}
            />
          </div>
        </div>
        <div className='row'>
          <div className='sixteen wide column'>
            <div className={cx('ui segment', !!riesgo.control_codigo || 'dimmable')}>
              <EditorPreview
                className='control-impacto-descripcion  descripcion-field'
                data={riesgo.control_impacto_descripcion}
                editable={editable}
                prefixPreview='Impacto en la Auditora segn la clasificacin del Riesgo Residual: '
                labelTitle='Impacto en la Auditora segn la clasificacin del Riesgo Residual:'
                openOnClickPreview={editable}
                onSave={this.onClickEditDescription('control_impacto_descripcion')}
                ckeditorUploadOptions={{ model: 'riesgo__impacto', modelId: riesgo.id }}
                footer='AttachmentList'
              />
              <RiesgoControlDimmer riesgo={riesgo} editable={editable} />
            </div>
          </div>
        </div>
      </Fragment>
    )

    return <ProyectoRiesgoModalSection header={header} content={content} />
  }

  render() {
    return (
      <Modal
        open
        dimmer='inverted'
        closeIcon
        onClose={this.onClose}
        closeOnDimmerClick={false}
        closeOnEscape={false}
        onMount={this.onModalMount}
        className='proyecto-riesgo-modal'
      >
        {this.renderSection1()}
        {this.renderSection2()}
        {this.renderSection3()}
        <Modal.Actions>
          <button className='ui button primary' onClick={this.props.onClose}>
            Aceptar
          </button>
        </Modal.Actions>
      </Modal>
    )
  }
}

type ProyectoRiesgoModalSectionProps = {
  header: JSX.Element
  content: JSX.Element
}

class ProyectoRiesgoModalSection extends PureComponent<ProyectoRiesgoModalSectionProps, { open: boolean }> {
  state = {
    open: true
  }
  onToggle = () => {
    this.setState((state) => {
      return { open: !state.open }
    })
  }

  render() {
    return (
      <section className={cx('modal-section', open ? 'open' : 'closed')}>
        <header className='modal-section-header'>
          <div className='ui middle aligned grid'>
            <div className='row'>
              <div className='fourteen wide column'>{this.props.header}</div>
              <div className='two wide column center aligned clickable toggle-collapse' onClick={this.onToggle}>
                {this.state.open ? (
                  <Fragment>
                    <i className='angle down large icon' title='Contraer' /> Contraer
                  </Fragment>
                ) : (
                  <Fragment>
                    <i className='angle left large icon' title='Expandir' /> Expandir
                  </Fragment>
                )}
              </div>
            </div>
          </div>
        </header>
        {this.state.open && <div className='section-body ui top aligned grid'>{this.props.content}</div>}
      </section>
    )
  }
}

type RiesgoFraudeMaterialCheckboxGroupProps = {
  checked: boolean
  editable: boolean
  onChange: (checked: boolean) => void
}

class RiesgoFraudeMaterialCheckboxGroup extends PureComponent<RiesgoFraudeMaterialCheckboxGroupProps> {
  onChange = (checked: boolean) => {
    if (!this.props.editable) {
      return
    }

    return () => {
      this.props.onChange(checked)
    }
  }
  render() {
    return (
      <div className={cx('riesgo-fraude-checkboxgroup', !this.props.editable ? 'disabled' : null)}>
        <label htmlFor='riesgofraudechk1' className={this.props.checked ? 'active' : ''} onClick={this.onChange(true)}>
          <span className='lb-title'>SI</span>
          <div className='riesgofraudechk' id='riesgofraudechk1' />
        </label>
        <label htmlFor='riesgofraudechk0' className={this.props.checked ? '' : 'active'} onClick={this.onChange(false)}>
          <span className='lb-title'>NO</span>
          <div className='riesgofraudechk' id='riesgofraudechk0' />
        </label>
      </div>
    )
  }
}

type BMACheckboxGroupProps = {
  value: 1 | 3 | 5
  tooltips?: any
}

class BMACheckboxGroup extends PureComponent<BMACheckboxGroupProps> {
  render() {
    const { value, tooltips } = this.props
    return (
      <div className={cx('bma-checkboxgroup', 'bma-value-' + this.props.value)}>
        <div className={cx('bma-checkbox-wrap', value == 1 ? 'active' : '', value >= 1 ? 'on' : '')}>
          <div className='bma-checkbox' /> <div className='checkbox-label'>Bajo {tooltips && tooltips[1]}</div>
        </div>
        <div className={cx('bma-checkbox-wrap', value == 3 ? 'active' : '', value >= 3 ? 'on' : '')}>
          <div className='bma-checkbox' />
          <div className='checkbox-label'>Medio {tooltips && tooltips[3]}</div>
        </div>
        <div className={cx('bma-checkbox-wrap', value == 5 ? 'active' : '', value == 5 ? 'on' : '')}>
          <div className='bma-checkbox' />
          <div className='checkbox-label'>Alto {tooltips && tooltips[5]}</div>
        </div>
      </div>
    )
  }
}

type RiesgoFieldSliderProps = {
  value: number
  labels: any
  onChange: (value: number) => void
  editable: boolean
  inverted?: boolean
}

class RiesgoFieldSlider extends PureComponent<RiesgoFieldSliderProps> {
  static defaultProps = {
    editable: true,
    inverted: false
  }
  onChange = (value: any) => {
    if (!this.props.editable) {
      return
    }

    if (value == 0) {
      return
    }

    return this.props.onChange(value)
  }

  render() {
    let { value } = this.props
    value = !value || value == 0 ? 0 : (+value + 1) / 2
    return (
      <div>
        <Slider
          min={0}
          max={3}
          tooltip={false}
          value={value}
          labels={this.props.labels}
          onChange={this.onChange}
          className={cx(
            `slider-value-${value || '0'}`,
            !this.props.inverted || 'inverted',
            this.props.editable || 'disabled'
          )}
        />
      </div>
    )
  }
}

const RiesgoControlDimmer = (props: { riesgo: ProyectoRiesgo; editable: boolean }) => {
  const { riesgo, editable } = props

  return (
    !riesgo.control_codigo && (
      <div className='ui dimmer active inverted riesgo-control-dimmer'>
        {editable && (
          <div className='content'>
            <div className='ui basic red label'>
              Diligencie el Cdigo del Control para habilitar la edicin de este campo.
            </div>
          </div>
        )}
      </div>
    )
  )
}

const RiesgoControlCodigoInput = (props: any) => {
  const { value: defaultValue, onChange } = props

  const [ value, setValue ] = useState<string>(defaultValue)

  const onSubmit = () => {
    if (value) {
      onChange(value)
    }
  }

  if (defaultValue && value != defaultValue) {
    setValue(defaultValue)
  }

  return defaultValue ? (
    <span>{defaultValue}</span>
  ) : (
    <Fragment>
      <div className='ui action mini input'>
        <input
          type='text'
          placeholder='ID del Control'
          className=''
          style={{ marginBottom: 0, width: 140 }}
          value={value || ''}
          onChange={(ev) => setValue(ev.target.value)}
          onBlur={onSubmit}
        />
        <button type='button' className='ui button compact icon green' onClick={onSubmit}>
          <i className='ui check icon large' />
        </button>
      </div>
    </Fragment>
  )
}

type PasoAssignModalProps = {
  riesgo: ProyectoRiesgo
  editable: boolean
  onSave: (selectedIds: string[]) => void
}

type PasoAssignModalState = {
  open: boolean
  selectedIds: string[]
}

class PasoAssignModal extends PureComponent<PasoAssignModalProps, PasoAssignModalState> {
  state: PasoAssignModalState = {
    open: false,
    selectedIds: []
  }

  componentDidMount() {
    this.setState({ selectedIds: _.pluck(this.props.riesgo.ProyectoRiesgoHasPaso, 'id_master_data_paso') })
  }

  submit = () => {
    this.props.onSave(this.state.selectedIds)
    this.setState({ open: false })
  }

  onChange = (selectedIds: string[]) => {
    this.setState({ selectedIds })
  }

  onClickDeleteItem = (id_paso: string) => {
    bootbox.warningConfirm(
      `Desea desvincular el paso "${app.vars.masterdatapasos[id_paso].descripcion}" de este riesgo?`,
      'Cancelar',
      'S, Desvincular paso',
      (answer) => {
        if (!answer) {
          return
        }

        this.setState((prevState) => {
          const selectedIds = _.intersection(prevState.selectedIds, [ id_paso ])
          this.props.onSave(selectedIds)
          return { selectedIds }
        })
      }
    )
  }

  render() {
    return (
      <div style={{ borderTop: '1px solid #CCC', marginTop: 5, paddingTop: 5 }}>
        {this.props.riesgo.ProyectoRiesgoHasPaso.length > 0 && (
          <strong className='title-field'>Pasos vinculados: </strong>
        )}
        {this.props.riesgo.ProyectoRiesgoHasPaso.length > 0 && (
          <div className='ui list horizontal' role='list'>
            {_.map(this.props.riesgo.ProyectoRiesgoHasPaso, (paso: any) => {
              return (
                <div className='item' key={`haspaso-${paso.id}`} role='listitem'>
                  <div className='ui label'>
                    <a
                      href={url_for(`proyecto/${app.vars.idproyecto}/master_data/paso/${paso.id_master_data_paso}`)}
                      target='_blank'
                    >
                      {app.vars.masterdatapasos[paso.id_master_data_paso].descripcion}
                    </a>
                    <i
                      aria-hidden='true'
                      className='delete icon'
                      onClick={() => this.onClickDeleteItem(paso.id_master_data_paso)}
                    />
                  </div>
                </div>
              )
            })}
          </div>
        )}

        {this.props.editable && (
          <button
            type='button'
            className='ui button mini compact primary inline'
            onClick={() => this.setState({ open: true })}
          >
            Vincular pasos
          </button>
        )}
        {this.state.open && (
          <Modal className='modal-ab' open onClose={() => this.setState({ open: false })}>
            <Modal.Header content={`Vincular Pasos al Riesgo ${this.props.riesgo.codigo}`} />
            <Modal.Content>
              <p className='description-text'>Seleccione los pasos que desea vincular a este riesgo</p>
              <PasoSelect selectedIds={this.state.selectedIds} onChange={this.onChange} />
            </Modal.Content>
            <Modal.Actions
              actions={[
                { content: 'Cancelar', negative: true, key: 'cancel', onClick: () => this.setState({ open: false }) },
                { content: 'Vincular pasos', primary: true, key: 'ok', onClick: this.submit }
              ]}
            />
          </Modal>
        )}
      </div>
    )
  }
}
