export class HallazgoRiesgoModelManager implements MenuElementModelManager<ProyectoHallazgo> {
  private static instance: HallazgoRiesgoModelManager

  static getInstance() {
    if (!HallazgoRiesgoModelManager.instance) {
      HallazgoRiesgoModelManager.instance = new this()
    }

    return HallazgoRiesgoModelManager.instance
  }

  /**
   * Descarga el listado de los hallazgos.
   *
   * @param props
   */
  getJson(props: DefaultMenuElementProps<ProyectoHallazgo>): Promise<ProyectoHallazgo[]> {
    return new Promise((resolve) => {
      $.getJSON(url_for2(`proyecto/:id_proyecto/riesgo_hallazgo/json`)).done((res) => {
        resolve(_.isEmpty(res.response) ? [] : res.response)
      })
    })
  }

  createItem(props: DefaultMenuElementProps<ProyectoHallazgo>, data: any): Promise<ProyectoHallazgo> {
    const url = url_for2(`proyecto/:id_proyecto/riesgo_hallazgo`)

    return new Promise<ProyectoHallazgo>((resolve) => {
      $.post(url, { data }).done((res) => {
        resolve(res.response.record)
      })
    })
  }

  saveItem(props: DefaultMenuElementProps<ProyectoHallazgo>, item: any, data: any): Promise<ProyectoHallazgo> {
    let url: string
    if (_.isEqual(data, { status: 'COMPLETADO' })) {
      url = `proyecto/:id_proyecto/riesgo_hallazgo/:id/c`
    } else if (_.isEqual(data, { status: 'REVISADO' })) {
      url = `proyecto/:id_proyecto/riesgo_hallazgo/:id/r`
    } else {
      url = `proyecto/:id_proyecto/riesgo_hallazgo/:id`
    }

    return new Promise<ProyectoHallazgo>((resolve) => {
      $.post(url_for2(url, { id: item.id }), { data }).done((res) => {
        resolve(res.response.record)
      })
    })
  }

  deleteItem(props: DefaultMenuElementProps<ProyectoHallazgo>, item: any): Promise<null> {
    const url = `proyecto/:id_proyecto/riesgo_hallazgo/:item_id`

    return new Promise<null>((resolve) => {
      $.ajax({ url: url_for2(url, { item_id: item.id }), type: 'DELETE' }).done((res) => {
        if (res.status == 1) {
          resolve()
        }
      })
    })
  }

  parseItems(items: any): any {
    return items
  }
}

export class RevisionRiesgoModelManager implements MenuElementModelManager<ProyectoRevisionTree> {
  private static instance: RevisionRiesgoModelManager

  static getInstance() {
    if (!RevisionRiesgoModelManager.instance) {
      RevisionRiesgoModelManager.instance = new this()
    }

    return RevisionRiesgoModelManager.instance
  }

  /**
   * Descarga el listado de los notas de revision.
   *
   * @param props
   */
  getJson(props: DefaultMenuElementProps<ProyectoRevisionTree>): Promise<ProyectoRevisionTree[]> {
    return new Promise((resolve) => {
      $.getJSON(url_for2(`proyecto/:id_proyecto/riesgo_revision`)).done((res) => {
        resolve(_.isEmpty(res.response.revisiones) ? [] : this.parseItems(res.response.revisiones))
      })
    })
  }

  createItem(props: DefaultMenuElementProps<ProyectoRevisionTree>, data: any): Promise<ProyectoRevisionTree> {
    const url = url_for2(`proyecto/:id_proyecto/riesgo_revision`)

    return new Promise<ProyectoRevisionTree>((resolve) => {
      $.post(url, data).done((res) => {
        resolve(res.response.revision)
      })
    })
  }

  saveItem(props: DefaultMenuElementProps<ProyectoRevisionTree>, item: any, data: any): Promise<ProyectoRevisionTree> {
    const url = url_for2(`proyecto/:id_proyecto/riesgo_revision/${item.id}`)

    return new Promise<ProyectoRevisionTree>((resolve) => {
      $.post(url, data).done((res) => {
        resolve(res.response.revision)
      })
    })
  }

  deleteItem(props: DefaultMenuElementProps<ProyectoRevisionTree>, item: any): Promise<null> {
    const url = `proyecto/:id_proyecto/riesgo_revision/:item_id`

    return new Promise<null>((resolve) => {
      $.ajax({ url: url_for2(url, { item_id: item.id }), type: 'DELETE' }).done((res) => {
        resolve()
      })
    })
  }

  parseItems(items: any): any {
    return items
  }
}
