import { PureComponent, SyntheticEvent } from 'react'
import cx from 'classnames'

import Dropdown from 'semantic-ui-react/dist/commonjs/modules/Dropdown/Dropdown'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup'

declare var app: any

class NivelesChoices {
  static BMA = { 1: 'Bajo', 3: 'Medio', 5: 'Alto' }
  static BMA2 = { 1: 'Bajo', 3: 'Moderado', 5: 'Alto' }
  static DMF = { 1: 'Dbil', 3: 'Moderado', 5: 'Fuerte' }
  static IDE = { 1: 'Inefectivo', 3: 'Con Deficiencias', 5: 'Efectivo' }
}

type RiesgoCellBlockProps = {
  style?: any
  className?: string
  loading?: boolean
  onClick?: () => void
}

export class RiesgoCellBlock extends PureComponent<RiesgoCellBlockProps> {
  static defaultProps = {
    loading: false
  }
  render() {
    if (this.props.loading) {
      return <div className='ui active centered inline tiny loader' />
    }

    return (
      <div className={cx('cell-block', this.props.className)} style={this.props.style} onClick={this.props.onClick}>
        {this.props.children}
      </div>
    )
  }
}

export function createRiesgoCellBlock(riesgo: ProyectoRiesgo, field: keyof ProyectoRiesgo, attrs?: any) {
  const getLabel = (input: any, cases: any) => {
    if (!input || input == '0') {
      return 'Por Definir'
    }

    return cases[input]
  }

  let choices
  switch (field) {
    case 'riesgo_inherente':
      choices = NivelesChoices.BMA
      break
    case 'riesgo_residual':
      choices = NivelesChoices.BMA2
      break
    case 'control_valoracion':
      choices = NivelesChoices.DMF
      break
    case 'control_efectividad':
      choices = NivelesChoices.IDE
      break
  }

  const value = riesgo[field] ? riesgo[field] : 0
  const className = cx(attrs ? attrs.className : null, `cell-block-value-${value}`, `field-${field}`)

  return (
    <RiesgoCellBlock className={className} {...attrs}>
      {getLabel(riesgo[field], choices)}
    </RiesgoCellBlock>
  )
}

type RiesgoFieldDropdownProps = {
  value: { id: string }[]
  field: string
  choices: any[]
  onChange: (value: any[]) => void
  editable?: boolean
}

export class RiesgoFieldDropdown extends PureComponent<RiesgoFieldDropdownProps> {
  choices: { key: string; text: string; value: string; tooltip?: string }[] = []

  static defaultProps = {
    editable: true
  }

  constructor(props: RiesgoFieldDropdownProps) {
    super(props)

    this.choices = _.map(this.props.choices, (item) => {
      return {
        key: item.id,
        text: 'name' in item ? item.name : item.descripcion,
        value: item.id,
        tooltip: item.description
      }
    })
  }

  parseValue() {
    return _.map(this.props.value, (item: any) => {
      return item[this.props.field]
    })
  }

  onClickRemoveItem = (key: string) => {
    const value = _.reject(this.props.value, { [this.props.field]: key })
    this.props.onChange(value)
  }

  onChange = (ev: any, params: any) => {
    let value = _.map(params.value, (item) => {
      return { [this.props.field]: item }
    })

    this.props.onChange(_.union(this.props.value, value))
  }

  render() {
    const values = this.parseValue()
    const choices = _.reject(this.choices, (choice) => {
      return _.include(values, choice.key)
    })

    let placeholder = null,
      dropdownText = null
    switch (this.props.value.length) {
      case 0:
        placeholder = 'Seleccionar'
        break
      case 1:
        dropdownText = '1 Seleccionado'
        break
      default:
        dropdownText = this.props.value.length + ' Seleccionados'
        break
    }

    return (
      <div className={cx('riesgo-field-dropdown', this.props.editable || 'disabled')}>
        {this.props.editable && (
          <Dropdown
            multiple
            selection
            options={choices}
            placeholder={placeholder}
            text={dropdownText}
            closeOnChange
            value={[]}
            onChange={this.onChange}
          />
        )}

        <ul className='riesgo-dropdown-selectedlist unstyled'>
          {_.map(this.props.value, (valueItem: any) => {
            const item = _.find(this.choices, { key: valueItem[this.props.field] })
            return (
              <Popup
                key={item.key}
                header={item.text}
                content={item.tooltip}
                disabled={!item.tooltip}
                size='mini'
                trigger={
                  <li className='dropdown-selectedlist-item'>
                    <span>{item.text}</span>
                    {this.props.editable && (
                      <i className='icon delete pull-right' onClick={() => this.onClickRemoveItem(item.key)} />
                    )}
                  </li>
                }
              />
            )
          })}
        </ul>
      </div>
    )
  }
}

type RiesgoCriterioEvaluacionControlTableProps = {
  riesgo: ProyectoRiesgo
  editable?: boolean
  onChange: (field: string, data: any) => void
}

type RiesgoCriterioEvaluacion = {
  label: string
  choices: {
    label: string
    value: string
  }[]
}
export class RiesgoCriterioEvaluacionControlTable extends PureComponent<RiesgoCriterioEvaluacionControlTableProps> {
  static defaultProps = {
    editable: true
  }

  getChoiceValue(choice: string) {
    const { riesgo }: { riesgo: any } = this.props
    const value = riesgo[choice]

    if (value === null || value === undefined) {
      return null
    }

    if (value === true) {
      return 1
    }

    return +value
  }

  render() {
    const criterios: RiesgoCriterioEvaluacion[] = app.vars.riesgocontext.criterios_evaluacion
    return (
      <table className='ui striped basic small table table-riesgo-criterios'>
        <thead>
          <tr>
            <th>
              <div className='criterio-name'>Criterio de Evaluacin</div>
            </th>
            <th>Valoracin</th>
          </tr>
        </thead>
        <tbody>
          {_.map(criterios, (criterio, k: string) => {
            const choices = _.map(criterio.choices, (choice, key) => {
              return {
                text: choice.label,
                value: +choice.value,
                key: key
              }
            })
            return (
              <tr key={k}>
                <td>
                  <div className='criterio-name'>{criterio.label}</div>
                </td>
                <td className='criterio-value'>
                  <RiesgoCriterioValoracionDropdown
                    choices={choices}
                    editable={this.props.editable}
                    value={this.getChoiceValue(k)}
                    onChange={(ev: SyntheticEvent, data: any) => this.props.onChange(k, data)}
                  />
                </td>
              </tr>
            )
          })}
        </tbody>
      </table>
    )
  }
}

const RiesgoCriterioValoracionDropdown = (props: any) => {
  const { choices, value, onChange, editable } = props
  const selectedChoice: { text: string; value: string } = value == null ? null : _.find(choices, { value })

  if (!editable) {
    return <div className='text'>{value == null ? '' : selectedChoice.text}</div>
  }

  const trigger =
    value == null ? <div className='default text'>Seleccione</div> : <div className='text'>{selectedChoice.text}</div>

  return (
    <Dropdown selectOnBlur={false} closeOnChange scrolling trigger={trigger} onChange={onChange} options={choices} />
  )
}
